import React, { Component } from 'react';
import Contact from './Contact';
import search from './img/search.svg';

class Contacts extends Component {

    state = {
        contacts: [{
            firstName: "Барней",
            lastName: "Стинсовський",
            phone: "+380956319521",
            gender: "male"
        }, {
            firstName: "Робін",
            lastName: "Щербатська",
            phone: "+380931460123",
            gender: "female"
        }, {
            firstName: "Анонімний",
            lastName: "Анонімус",
            phone: "+380666666666",
            gender: "undefined"
        }, {
            firstName: "Лілія",
            lastName: "Олдровна",
            phone: "+380504691254",
            gender: "female"
        }, {
            firstName: "Маршен",
            lastName: "Еріксонян",
            phone: "+380739432123",
            gender: "male"
        }, {
            firstName: "Теодор",
            lastName: "Мотсбес",
            phone: "+380956319521",
            gender: "male"
        }],
        search: '',
    };
    handleSearchChange = e => {
        this.setState({ search: e.target.value });
    }

    render() {
        const filteredContacts = this.state.contacts.filter(e =>
            e.lastName.toLowerCase().includes(this.state.search.toLowerCase())
            || e.firstName.toLowerCase().includes(this.state.search.toLowerCase()) ||
            e.phone.toLowerCase().includes(this.state.search.toLowerCase()) ||
            e.gender.toLowerCase().includes(this.state.search.toLowerCase()));

        if (this.state.search === (!this.state.search)) {
            filteredContacts = this.state.contacts;
        } else {
            filteredContacts.map((contact) => <Contact {...contact} key={contact.index} />);
        }

        return (

            <section className="contacts-wrapper">
                <div className="search-wrapper">
                    <input type="text" placeholder="Input name/gender/phone"
                        value={this.state.search} onChange={this.handleSearchChange}></input>
                </div>
                <div className="checkboxes-wrapper">
                    <span>male</span><input type="checkbox" id="male" checked="checked"></input>
                    <span>female</span><input type="checkbox" id="female" checked="checked"></input>
                    <span>undefined</span><input type="checkbox" id="undefined" checked="checked"></input>
                </div>
                {filteredContacts.map((contact) => <Contact {...contact} key={contact.index} />)}
            </section>
        )
    }

}

export default Contacts;